@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");
.about__container {
  padding: 85px 61px;
  display: flex;
  flex-wrap: wrap;
  min-width: 300px;
  flex-direction: column;
  align-items: center;
  visibility: hidden;
  z-index: 3;
  justify-content: center;
}
.about__Content {
  line-height: 2;
  letter-spacing: 1px;
  text-align: justify;
  word-spacing: 2px;
}
.about__name {
  
  visibility: hidden;
}
.about__nameDark {
  color: white;
}
.about__name > span {
  font-size: 50px;
  font-family: "Dancing Script", cursive;
  font-weight: 900;
}
.about__nameDark > h2 {
  color: white;
}
.about__rightSide{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 0.4;
}
.about__mainContent {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  padding: 10px 12px;
  flex-wrap: wrap;
}
.about__mainContent p {
  font-size: 16px;
  font-weight: 500;
  color: #1f1235;
  transition: 1s all linear;
}
.btn {
  max-width: max-content;

  margin: 10px 10px;
  visibility: hidden;
}
.about__highlights {
  font-weight: 600;
}

.about__myImg {
  object-fit: contain;
  height: 120px;
  border-radius: 100px;
  cursor: pointer;
  margin-bottom: 30px;
  transition: 1s all ease-in-out;
}
.aboutImg__div{
  overflow: hidden;
  /* flex:0.4; */
  /* display:flex;
  flex-direction:row-reverse; */
}
.about__paraDiv{
  overflow: hidden;
  min-width: 280px;
  flex:0.6 ;
}
.about__myImg:hover {
  box-shadow: 0px 1px 11px 4px rgb(0 0 0 / 50%);
}
.about__btn {
  min-width: 110px;
  text-align: center;
  border: none;
  min-height: 40px;
  border-radius: 3px;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid black;
  background-color: #ff6e6c;
  transition: 1s all ease-out;
}
.about__btn:focus {
  outline: none;
}
.about__btn:hover {
  box-shadow: 0px 1px 11px 4px rgb(0 0 0 / 50%);
}
.about__mainContent .about__paraContent {
  color: white;
}
.about__btnDiv {
  margin-left: 50px;
  position: fixed;
  /* right: 4px; */
  bottom: 4px;
  z-index: 100;
}

@media all and (max-width: 500px) {
  .about__container {
   padding: 0px;
  }
  .about__mainContent {
    /* margin-left: 20px; */
    padding: 0px;
    padding-top: 20px;
  }
  .about__rightSide{
    padding-bottom: 15px;
  }
  .about__btnDiv {
    margin-left: 20px;
  }
  .about__name > span {
    font-size: 40px;
  }
}
